/* ============================================================================
 * FONTS
 * ============================================================================ */
@include font(regular, 'Montserrat-Regular');
@include font(medium, 'Montserrat-Medium');
@include font(semibold, 'Montserrat-SemiBold');
@include font(bold, 'Montserrat-Bold');

@mixin Menu_text_desktop {
  font-family: bold;
  font-size: 13px;
  text-transform: uppercase; }
/* ============================================================================
 * COLORS
 * ============================================================================ */
$green1: #3ECC29;
$green2: #00D242;
$gray2: #808080;
$gray3: #A1A1A1;
$footer: #3C3C3C;
