html {
  font-size: 10px; }
body {
  background: #FFF;
  // +css-lock(16, 30, 600, 1280)
  line-height: 1.4;
  font-family: regular;
  margin-top: 80px;
  @include r(1300) {
    margin-top: 109px; }
  @include r(650) {
    margin-top: 71px; } }

::selection {
  background: #ccc; }
::-moz-selection {
  background: #ccc; }
::-webkit-selection {
  background: #ccc;
  color:#fff {} }

a {
  text-decoration: none;
  color: #000; }

.container {
  @include maw(1280);
  margin: 0 auto;
  @include r(1300) {
    padding-left: 24px;
    padding-right: 24px; } }
/* ============================================================================
 * HEADER START
 * ============================================================================ */
header.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.08);
  background-color: #fff;
  padding: 0 20px;
  .header-wrapper.flex {
    @include maw(1340);
    margin-left: auto;
    margin-right: auto;
    justify-content: space-between;
    align-items: center;
    .logo-wrapper {
      align-items: center;
      @include r(1300) {
        height: 56px; }
      @include r(650) {
        height: 40px; }
      img {
        height: 25px;
        width: 112.5px; }
      p {
        margin-left: 20px;
        padding-left: 20px;
        border-left: 1px solid $gray2;
        font-size: 11px;
        font-family: medium;
        color: $gray2;
        @include maw(183);
        @include r(1300) {
          height: 100%;
          align-items: center;
          line-height: 19px;
          padding-top: 10px; }
        @include r(650) {
          display: none; } } }
    nav {
      @include r(1300) {
        display: none; } }
    ul {
      li {
        line-height: 100%;
        margin-right: 25px;
        height: 80px;
        a {
          @include Menu_text_desktop;
          display: inline-block;
          height: 100%;
          line-height: 80px;
          border-bottom: 4px solid #fff;
          @include tr(0.5s border-bottom ease-in-out);
          &:hover {
            border-bottom: 4px solid $green1; } }
        &:last-of-type {
          margin-right: 0; } } }
    a.tel {
      font-family: medium;
      color: $gray3;
      font-size: 20px;
      display: flex;
      align-items: center;
      @include r(1300) {
        display: none; } }
    button {
      background-color: $green1;
      color: #fff;
      font-family: medium;
      font-size: 15px;
      border-radius: 2px;
      height: 40px;
      padding: 0 30px;
      margin: 20px 0;
      @include r(1300) {
        margin: 27px 0;
        height: 55px; }
      @include r(650) {
        display: none; } } }
  // HAMBURGER
  .nav-icon-wrapper {
    @include r(1300) {
      display: flex;
      align-items: center; }
    #nav-icon {
      width: 35px;
      height: 23px;
      position: relative;
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
      -webkit-transition: .5s ease-in-out;
      -moz-transition: .5s ease-in-out;
      -o-transition: .5s ease-in-out;
      transition: .5s ease-in-out;
      cursor: pointer;
      margin-top: 27px;
      margin-bottom: 21px;
      span {
        display: block;
        position: absolute;
        height: 3px;
        width: 100%;
        background: $green1;
        opacity: 1;
        left: 0;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
        -webkit-transition: .25s ease-in-out;
        -moz-transition: .25s ease-in-out;
        -o-transition: .25s ease-in-out;
        transition: .25s ease-in-out;
        &:nth-child(1) {
          top: 0px;
          -webkit-transform-origin: left center;
          -moz-transform-origin: left center;
          -o-transform-origin: left center;
          transform-origin: left center; }
        &:nth-child(2) {
          top: 7px;
          -webkit-transform-origin: left center;
          -moz-transform-origin: left center;
          -o-transform-origin: left center;
          transform-origin: left center; }
        &:nth-child(3) {
          top: 14px;
          -webkit-transform-origin: left center;
          -moz-transform-origin: left center;
          -o-transform-origin: left center;
          transform-origin: left center; } } }
    #nav-icon.open {
      transform: rotate(90deg); }
    .menu-name {
      margin-left: 15px;
      font-family: medium;
      font-size: 20px;
      @include r(768) {
        font-size: 16px; } } } }
// Header burger menu
.burger-menu-wrapper {
  background-color: #ffffff;
  position: fixed;
  left: 0;
  right: 0;
  top: -200%;
  bottom: 200%;
  overflow: auto;
  opacity: 0;
  @include tr(all 0.5s ease-in-out);
  .burger-content {
    @include maw(460);
    margin: 60px auto 90px;
    @include r(500) {
      margin: 15px auto; } }
  .nav-wrapper {
    height: 380px;
    @include r(500) {
      height: auto; } }
  nav {
    display: flex;
    justify-content: space-between;
    height: 100%;
    @include r(500) {
      flex-direction: column; }
    ul {
      max-width: 167px;
      flex-direction: column;
      @include r(500) {
        @include maw(227);
        margin: 0 auto; }
      li {
        margin: 30px 0;
        @include r(500) {
          margin: 20px 0; }
        a {
          text-transform: uppercase;
          font-family: bold;
          font-size: 13px;
          color: #000;
          @include tr(0.5s color ease-in-out);
          &:hover {
            color: $green1; } } } } }
  .price-list {
    &:before {
      content: ' ';
      background-image: url('../img/footer-pdf-black.svg');
      height: 20px;
      width: 20px;
      background-size: 20px 20px;
      background-repeat: no-repeat;
      display: inline-block;
      margin-right: 20px;
      vertical-align: -5px; } }
  .line {
    width: 1px;
    height: 100%;
    background-color: #E1E1E1;
    @include r(500) {
      display: none; } } }
.burger-menu-wrapper.open {
  top: 109px;
  bottom: 0;
  opacity: 1;
  @include r(650) {
    top: 71px; } }
/* ============================================================================
 * HEADER END
 * ============================================================================ */
/* ============================================================================
 * FOOTER START
 * ============================================================================ */
footer.footer {
  padding: 50px 0 30px;
  background-color: $footer;
  @include r(768) {
    padding: 40px 0 30px; }
  @include r(375) {
    padding: 40px 0 20px; }
  .menu {
    margin-bottom: 40px;
    border-bottom: 1px solid $gray2;
    @include r(375) {
      margin-bottom: 30px; } }
  ul {
    justify-content: space-between;
    @include r(1300) {
      flex-wrap: wrap;
      justify-content: flex-start; }
    li {
      margin-right: 25px;
      margin-bottom: 60px;
      @include r(768) {
        margin-bottom: 40px; }
      @include r(375) {
        margin-bottom: 30px; }
      a {
        @include Menu_text_desktop;
        display: inline-block;
        @include tr(0.5s color ease-in-out);
        color: #fff;
        &:hover {
          color: $green1; } }
      &:last-of-type {
        margin-right: 0; } } }
  .price-list {
    border-left: 1px solid #ffffff;
    padding-left: 20px;
    &:before {
      content: ' ';
      background-image: url('../img/footer-pdf.svg');
      height: 20px;
      width: 20px;
      background-size: 20px 20px;
      background-repeat: no-repeat;
      display: inline-block;
      margin-right: 20px;
      vertical-align: -5px; } }
  .row.flex {
    justify-content: space-between;
    align-items: center;
    color: $gray2;
    font-size: 13px;
    @include r(768) {
      font-size: 12px; }
    @include r(700) {
      flex-direction: column; }
    a {
      text-decoration: underline;
      color: $gray2;
      font-size: 13px;
      @include r(700) {
        order: 2; }
      @include r(768) {
        font-size: 12px; } }
    .social-wrapper {
      @include r(700) {
        order: 1;
        margin-bottom: 40px; }
      a {
        text-decoration: none;
        margin-right: 30px;
        &:last-of-type {
          margin-right: 0; } } }
    p {
      @include r(700) {
        order: 3;
        margin-top: 30px; } } } }
/* ============================================================================
 * FOOTER END
 * ============================================================================ */
