html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

/* ============================================================================
 * FONTS
 * ============================================================================ */
@font-face {
  font-family: regular;
  src: url("../fonts/Montserrat-Regular.ttf") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: medium;
  src: url("../fonts/Montserrat-Medium.ttf") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: semibold;
  src: url("../fonts/Montserrat-SemiBold.ttf") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: bold;
  src: url("../fonts/Montserrat-Bold.ttf") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

/* ============================================================================
 * COLORS
 * ============================================================================ */
.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: none;
}

html {
  font-size: 10px;
}

body {
  background: #FFF;
  line-height: 1.4;
  font-family: regular;
  margin-top: 80px;
}

@media only screen and (max-width: 1300px) {
  body {
    margin-top: 109px;
  }
}

@media only screen and (max-width: 650px) {
  body {
    margin-top: 71px;
  }
}

::selection {
  background: #ccc;
}

::-moz-selection {
  background: #ccc;
}

::-webkit-selection {
  background: #ccc;
  color: #fff;
}

a {
  text-decoration: none;
  color: #000;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}

@media only screen and (max-width: 1300px) {
  .container {
    padding-left: 24px;
    padding-right: 24px;
  }
}

/* ============================================================================
 * HEADER START
 * ============================================================================ */
header.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.08);
  background-color: #fff;
  padding: 0 20px;
}

header.header .header-wrapper.flex {
  max-width: 1340px;
  margin-left: auto;
  margin-right: auto;
  justify-content: space-between;
  align-items: center;
}

header.header .header-wrapper.flex .logo-wrapper {
  align-items: center;
}

@media only screen and (max-width: 1300px) {
  header.header .header-wrapper.flex .logo-wrapper {
    height: 56px;
  }
}

@media only screen and (max-width: 650px) {
  header.header .header-wrapper.flex .logo-wrapper {
    height: 40px;
  }
}

header.header .header-wrapper.flex .logo-wrapper img {
  height: 25px;
  width: 112.5px;
}

header.header .header-wrapper.flex .logo-wrapper p {
  margin-left: 20px;
  padding-left: 20px;
  border-left: 1px solid #808080;
  font-size: 11px;
  font-family: medium;
  color: #808080;
  max-width: 183px;
}

@media only screen and (max-width: 1300px) {
  header.header .header-wrapper.flex .logo-wrapper p {
    height: 100%;
    align-items: center;
    line-height: 19px;
    padding-top: 10px;
  }
}

@media only screen and (max-width: 650px) {
  header.header .header-wrapper.flex .logo-wrapper p {
    display: none;
  }
}

@media only screen and (max-width: 1300px) {
  header.header .header-wrapper.flex nav {
    display: none;
  }
}

header.header .header-wrapper.flex ul li {
  line-height: 100%;
  margin-right: 25px;
  height: 80px;
}

header.header .header-wrapper.flex ul li a {
  font-family: bold;
  font-size: 13px;
  text-transform: uppercase;
  display: inline-block;
  height: 100%;
  line-height: 80px;
  border-bottom: 4px solid #fff;
  transition: 0.5s border-bottom ease-in-out;
}

header.header .header-wrapper.flex ul li a:hover {
  border-bottom: 4px solid #3ECC29;
}

header.header .header-wrapper.flex ul li:last-of-type {
  margin-right: 0;
}

header.header .header-wrapper.flex a.tel {
  font-family: medium;
  color: #A1A1A1;
  font-size: 20px;
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 1300px) {
  header.header .header-wrapper.flex a.tel {
    display: none;
  }
}

header.header .header-wrapper.flex button {
  background-color: #3ECC29;
  color: #fff;
  font-family: medium;
  font-size: 15px;
  border-radius: 2px;
  height: 40px;
  padding: 0 30px;
  margin: 20px 0;
}

@media only screen and (max-width: 1300px) {
  header.header .header-wrapper.flex button {
    margin: 27px 0;
    height: 55px;
  }
}

@media only screen and (max-width: 650px) {
  header.header .header-wrapper.flex button {
    display: none;
  }
}

@media only screen and (max-width: 1300px) {
  header.header .nav-icon-wrapper {
    display: flex;
    align-items: center;
  }
}

header.header .nav-icon-wrapper #nav-icon {
  width: 35px;
  height: 23px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
  margin-top: 27px;
  margin-bottom: 21px;
}

header.header .nav-icon-wrapper #nav-icon span {
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  background: #3ECC29;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

header.header .nav-icon-wrapper #nav-icon span:nth-child(1) {
  top: 0px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

header.header .nav-icon-wrapper #nav-icon span:nth-child(2) {
  top: 7px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

header.header .nav-icon-wrapper #nav-icon span:nth-child(3) {
  top: 14px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

header.header .nav-icon-wrapper #nav-icon.open {
  transform: rotate(90deg);
}

header.header .nav-icon-wrapper .menu-name {
  margin-left: 15px;
  font-family: medium;
  font-size: 20px;
}

@media only screen and (max-width: 768px) {
  header.header .nav-icon-wrapper .menu-name {
    font-size: 16px;
  }
}

.burger-menu-wrapper {
  background-color: #ffffff;
  position: fixed;
  left: 0;
  right: 0;
  top: -200%;
  bottom: 200%;
  overflow: auto;
  opacity: 0;
  transition: all 0.5s ease-in-out;
}

.burger-menu-wrapper .burger-content {
  max-width: 460px;
  margin: 60px auto 90px;
}

@media only screen and (max-width: 500px) {
  .burger-menu-wrapper .burger-content {
    margin: 15px auto;
  }
}

.burger-menu-wrapper .nav-wrapper {
  height: 380px;
}

@media only screen and (max-width: 500px) {
  .burger-menu-wrapper .nav-wrapper {
    height: auto;
  }
}

.burger-menu-wrapper nav {
  display: flex;
  justify-content: space-between;
  height: 100%;
}

@media only screen and (max-width: 500px) {
  .burger-menu-wrapper nav {
    flex-direction: column;
  }
}

.burger-menu-wrapper nav ul {
  max-width: 167px;
  flex-direction: column;
}

@media only screen and (max-width: 500px) {
  .burger-menu-wrapper nav ul {
    max-width: 227px;
    margin: 0 auto;
  }
}

.burger-menu-wrapper nav ul li {
  margin: 30px 0;
}

@media only screen and (max-width: 500px) {
  .burger-menu-wrapper nav ul li {
    margin: 20px 0;
  }
}

.burger-menu-wrapper nav ul li a {
  text-transform: uppercase;
  font-family: bold;
  font-size: 13px;
  color: #000;
  transition: 0.5s color ease-in-out;
}

.burger-menu-wrapper nav ul li a:hover {
  color: #3ECC29;
}

.burger-menu-wrapper .price-list:before {
  content: ' ';
  background-image: url("../img/footer-pdf-black.svg");
  height: 20px;
  width: 20px;
  background-size: 20px 20px;
  background-repeat: no-repeat;
  display: inline-block;
  margin-right: 20px;
  vertical-align: -5px;
}

.burger-menu-wrapper .line {
  width: 1px;
  height: 100%;
  background-color: #E1E1E1;
}

@media only screen and (max-width: 500px) {
  .burger-menu-wrapper .line {
    display: none;
  }
}

.burger-menu-wrapper.open {
  top: 109px;
  bottom: 0;
  opacity: 1;
}

@media only screen and (max-width: 650px) {
  .burger-menu-wrapper.open {
    top: 71px;
  }
}

/* ============================================================================
 * HEADER END
 * ============================================================================ */
/* ============================================================================
 * FOOTER START
 * ============================================================================ */
footer.footer {
  padding: 50px 0 30px;
  background-color: #3C3C3C;
}

@media only screen and (max-width: 768px) {
  footer.footer {
    padding: 40px 0 30px;
  }
}

@media only screen and (max-width: 375px) {
  footer.footer {
    padding: 40px 0 20px;
  }
}

footer.footer .menu {
  margin-bottom: 40px;
  border-bottom: 1px solid #808080;
}

@media only screen and (max-width: 375px) {
  footer.footer .menu {
    margin-bottom: 30px;
  }
}

footer.footer ul {
  justify-content: space-between;
}

@media only screen and (max-width: 1300px) {
  footer.footer ul {
    flex-wrap: wrap;
    justify-content: flex-start;
  }
}

footer.footer ul li {
  margin-right: 25px;
  margin-bottom: 60px;
}

@media only screen and (max-width: 768px) {
  footer.footer ul li {
    margin-bottom: 40px;
  }
}

@media only screen and (max-width: 375px) {
  footer.footer ul li {
    margin-bottom: 30px;
  }
}

footer.footer ul li a {
  font-family: bold;
  font-size: 13px;
  text-transform: uppercase;
  display: inline-block;
  transition: 0.5s color ease-in-out;
  color: #fff;
}

footer.footer ul li a:hover {
  color: #3ECC29;
}

footer.footer ul li:last-of-type {
  margin-right: 0;
}

footer.footer .price-list {
  border-left: 1px solid #ffffff;
  padding-left: 20px;
}

footer.footer .price-list:before {
  content: ' ';
  background-image: url("../img/footer-pdf.svg");
  height: 20px;
  width: 20px;
  background-size: 20px 20px;
  background-repeat: no-repeat;
  display: inline-block;
  margin-right: 20px;
  vertical-align: -5px;
}

footer.footer .row.flex {
  justify-content: space-between;
  align-items: center;
  color: #808080;
  font-size: 13px;
}

@media only screen and (max-width: 768px) {
  footer.footer .row.flex {
    font-size: 12px;
  }
}

@media only screen and (max-width: 700px) {
  footer.footer .row.flex {
    flex-direction: column;
  }
}

footer.footer .row.flex a {
  text-decoration: underline;
  color: #808080;
  font-size: 13px;
}

@media only screen and (max-width: 700px) {
  footer.footer .row.flex a {
    order: 2;
  }
}

@media only screen and (max-width: 768px) {
  footer.footer .row.flex a {
    font-size: 12px;
  }
}

@media only screen and (max-width: 700px) {
  footer.footer .row.flex .social-wrapper {
    order: 1;
    margin-bottom: 40px;
  }
}

footer.footer .row.flex .social-wrapper a {
  text-decoration: none;
  margin-right: 30px;
}

footer.footer .row.flex .social-wrapper a:last-of-type {
  margin-right: 0;
}

@media only screen and (max-width: 700px) {
  footer.footer .row.flex p {
    order: 3;
    margin-top: 30px;
  }
}

/* ============================================================================
 * FOOTER END
 * ============================================================================ */
.flex {
  display: flex;
}

.dnone {
  display: none;
}
